export default {
	data () { //引入PopupPicker组件 引入提现路由
		return {
			popupArr: [[]],
			moneyArr: [],
			DrId: this.$store.state.userInfo == null ? 18 : this.$store.state.userInfo.Data.User.DoctorId,
			timeFn: [],
			AvailableIncome: 0, //可提现
			UnconfirmIncome: 0, //待确定
			TotalIncome: 0, //总收入
			moneyCss: {width: ''},
			type: Boolean,
			default: false,
			scrollStart: false
		}
	},
	methods: {
		back(){
			this.$store.dispatch('pushcMed', null)
			const now = location.href.split('?')[0]
			const start = this.$store.state.startUrl
			if(now === start){
				const data = '123'
				this.$bridge.callhandler('Back', data)
			}
			else{
				this.$router.back()
			}
		},
		onChange (value) { //获取时间组件数据
			this.$refs.scroll.scrollTo(0, 0)
			this.incomeYearFn(value[0])
		},
		IncomeRecordFn () {
			this.$router.push({
				path: '/Center/IncomeRecord',
				name: 'IncomeRecord',
				params: {
					id: this.DrId,
				}
			})
		},
		cardFn () {
			this.$router.push({
				path: '/Center/card',
				name: 'card',
				params: {
					id: this.DrId,
				}
			})
		},
		incomeFn () { //获取可提现、待确定、总金额
			this.$get('/PlatFormAPI/Doctor/QueryDoctorInSummaryByDrId',{
				DrId: this.DrId
			}).then(o => {
				if(o != null && o != undefined && o != []){
					this.AvailableIncome = o.AvailableIncome
					this.UnconfirmIncome = o.UnconfirmIncome
					this.TotalIncome = o.TotalIncome
				}
			})
		},
		incomeYearFn (t) { //根据年份获取收入
			let arr = [
				{
					InMonth: 1,
					MonSum: 0.0
				},
				{
					InMonth: 2,
					MonSum: 0.0
				},
				{
					InMonth: 3,
					MonSum: 0.0
				},
				{
					InMonth: 4,
					MonSum: 0.0
				},
				{
					InMonth: 5,
					MonSum: 0.0
				},
				{
					InMonth: 6,
					MonSum: 0.0
				},
				{
					InMonth: 7,
					MonSum: 0.0
				},
				{
					InMonth: 8,
					MonSum: 0.0
				},
				{
					InMonth: 9,
					MonSum: 0.0
				},
				{
					InMonth: 10,
					MonSum: 0.0
				},
				{
					InMonth: 11,
					MonSum: 0.0
				},
				{
					InMonth: 12,
					MonSum: 0.0
				},
			]
			this.$get('/PlatFormAPI/Doctor/QueryDoctorMonSummaryByDrId',{
				DrId: this.DrId,
				InYear: t
			}).then(o => {
				this.moneyArr = []
				this.scrollStart = true
				this.moneyCss.width = (o.length * 136.8) / 75 + 'rem'
				o.forEach((n,k) => {
					arr.forEach((v,i) => {
						if(v.InMonth == n.InMonth){
							arr[i] = o[k]
						}
					})
				})
				this.moneyArr = arr;
				let date = new Date
				const year = date.getFullYear()
				const month = date.getMonth()
				setTimeout(() => {
					if(this.timeFn[0] == year){
						if(month >= 4){
							this.$refs.scroll.scrollTo((month-4)*80, 0)
						}
					}
				}, 100)
			})
		},
		NumFormat (m) { //获取金额 3位添加逗号
			if(m == 0){
				return '0.00'
			}else{
				return m
			}
			if(!m) return '0.00';
			return Number(m).toFixed(0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
		},
		routerLinkFn () {
			this.$router.push({
				path: '/Center/deposit',
				name: 'deposit',
				params: {
					id: this.DrId,
				}
      		})
		}
	},
	created(){
		let date = new Date
		const year = date.getFullYear()
		this.timeFn[0] = year
		const long = year - 2018
		for(let i = 0 ; i<=long; i++){
			this.popupArr[0].push(year-i)
		}
		this.incomeFn();
		this.incomeYearFn(this.timeFn[0]);
	}
}



